.sidebar{
    flex: 1;
    height: calc(100vh - 50px);
    background-color: rgb(251, 251, 255);
    position: sticky;
    top: 50px;

    .sidebarWrapper{
        padding: 20px;
        color: #555;


        .sidebarMenu{
            margin-bottom: 10px;
            .sidebarTitle{
                color: rgb(187, 186 ,186);
                font-size: 13px;
            }
            .sidebarList{
                list-style: none;
                padding: 5px;
            .sidebarLink{
                text-decoration: none;
                color: inherit;
            }


                .sidebarListItem{
                    padding: 5px;
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    border-radius: 10px;
                    
                    .sidebarIcon{
                        margin-right: 5px;
                        font-size: 20px !important;
                    }
                }

                .sidebarListItem.active,
                .sidebarListItem:hover{
                    background-color: rgb(240, 240, 225);
                }
            }
        }
    }
}